

























































import Vue from "vue";
import Swiper from "swiper";
import "swiper/dist/css/swiper.min.css";
import { mapActions, mapState, mapGetters, mapMutations } from "vuex";
export default Vue.extend({
  props: {},
  components: {},
  data() {
    return {
      isShow: true, //
      isSwiper: false, //
      swiperIndex: 1,
      Page: 1,
      PageSize: 30,
    };
  },
  computed: {
    ...mapState({
      pictureList: (state: any) => state.picture.pictureList,
      detailPictureList: (state: any) => state.picture.detailPictureList,
      getColorId:(state:any)=>state.picture.getColorId,
      getTypeId:(state:any)=>state.picture.getTypeId
    }),
    ...mapGetters({
      sortArr:"car/sortArr",
    })
  },
  methods: {
    ...mapActions({
      getPicture: "picture/getPicture",
      toDetail: "picture/toDetail",
      getColorPicture: "picture/getColorPicture",
      getSwiper: "picture/getSwiper",
      getPage: "picture/getPage"
    }),
    //
    goToDetail(isSwiper: number, ImageID: string, ind: number) {
     
      if (!isSwiper) {
        this.isShow = false;
      } else {
        this.mySwiper(ind);
      }
       this.toDetail({ ImageID, ColorID: this.getColorId.ColorId});
    },
    //
    mySwiper(ind: number) {
      let that = this;
      that.isSwiper = true;
      that.$nextTick(() => {
        let mySwiper = new Swiper(that.$refs.swiper, {
          observer: true,
          on: {
            slideChangeTransitionEnd: function() {
              //slide
              that.swiperIndex = this.activeIndex + 1;
              if (that.swiperIndex === that.Page * that.PageSize - 5) {
                that.getPage({
                  ColorID: that.getColorId.ColorId,
                   Page:++that.Page,
                   PageSize:that.PageSize
                });
              }
            }
          }
        });
        mySwiper.slideTo(ind, 1); //slide
      });
    },
    //swiper
    removeSwiper() {
      this.isSwiper = false;
    },
    //
    imgList() {
      //
      let innerHeight = this.$refs.imgListScroll.clientHeight;
      //
      let outerHeight = this.$refs.imgList.clientHeight;
      //
      let scrollTop = this.$refs.imgList.scrollTop;
      //scrollTop0innerHeight < (outerHeight + scrollTop)
      // console.log(innerHeight + " " + outerHeight + " " + scrollTop);
      if (innerHeight < outerHeight + scrollTop) {
        // //
        clearTimeout(this.timer);
        this.timer = setTimeout(() => {
          this.getPage({
            ColorID: this.getColorId.ColorId,
            Page:++this.Page,
            PageSize:this.PageSize
          });
        }, 500);
      }
    },
    //
    low(){
      this.$router.push({
        name:"quotation",
        params:{
          car_id:this.getTypeId.car_id||this.sortArr[0].list[0].car_id
        }
      })
    }
  },
  created() {
     this.timer=0
     this.getColorPicture();
  },
  updated() {},
  mounted() {
    console.log("deta",this.getTypeId)
  }
});
